@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  padding: 16px 70px;
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  background: #ffffff;
  border: 2px solid #444444;
  text-decoration: none;
}

.button:hover {
  background: #444444;
  border: 2px solid #444444;
  color: #ffffff;
}

.button:active {
  background: #444444;
  border: 2px solid #444444;
  color: rgba(255, 255, 255, 0.3);
}

.link {
  text-decoration: none;
}

/* добавить */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  margin: 0;
  flex-direction: column;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  color: #000000;
  background-color: #ffffff;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

img {
  max-width: 100%;
  height: auto;
}

@media (min-width: 768px) {
  .container {
    max-width: 668px;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1051px;
    padding: 0;
  }
}
.page-footer {
  background-color: #f1f1f1;
  cursor: pointer;
}

.page-footer__logo {
  display: none;
}

.page-footer__logo-icon {
  fill: #000000;
}

.page-footer__logo-icon:hover {
  fill: #46c1ae;
}

.page-footer__logo-icon:active {
  fill: #46c1ae;
  opacity: 0.3;
}

.page-footer__social {
  width: 100%;
  margin-top: 27px;
  margin-bottom: 18px;
  padding-bottom: 27px;
  border-bottom: 1px solid #e5e5e5;
}

.page-footer__copyright {
  display: flex;
  justify-content: center;
  gap: 9%;
  align-items: center;
  margin-bottom: 22px;
}

@media (min-width: 768px) {
  .page-footer__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 0 53px;
  }

  .page-footer__logo {
    display: block;
  }

  .page-footer__social {
    margin: 0 auto;
    margin-right: 10px;
    padding: 0;
  }
}
.page-header__nav {
  position: relative;
}

.page-header__nav-inner {
  box-shadow: inset 0 1px 0 #ececec;
  background-color: #f9f9f9;
}

.social__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 11%;
  list-style: none;
}

.social__link {
  width: 28px;
  height: 22px;
}

.social__link-icon {
  fill: #231f20;
}

.social__link-icon:hover {
  fill: #46c1ae;
}

.social__link-icon:active {
  fill: #46c1ae;
  opacity: 0.3;
}

@media (min-width: 768px) {
  .page-footer__social {
    border: none;
  }

  .social__list {
    gap: 8%;
  }
}
@media (min-width: 1150px) {
  .social__list {
    gap: 4%;
  }
}
.copyright__title {
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  margin: 0;
  font-size: 19px;
  line-height: 18px;
}

.copyright__link-icon {
  margin-top: 3px;
  fill: #63d1bb;
}

.copyright__link-icon:hover {
  fill: #46c1ae;
}

.copyright__link-icon:active {
  fill: #46c1ae;
  opacity: 0.3;
}

@media (min-width: 768px) {
  .page-footer__copyright {
    margin: 0 13px 0 0;
    gap: 18%;
    align-items: flex-start;
  }

  .copyright__title {
    margin-top: 7px;
  }
}
@media (min-width: 1150px) {
  .copyright__link-icon {
    margin: 0;
  }
}
.modal {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  /* Добавить цвет*/
}

.modal-close {
  display: none;
}

.modal__container {
  position: relative;
  margin: auto;
  padding: 1.75rem;
  padding-bottom: 3rem;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  background-color: #ffffff;
}

.modal__title {
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  max-width: 150px;
  margin: auto;
  margin-bottom: 14px;
  font-size: 25px;
  line-height: 1.2em;
  text-transform: uppercase;
  text-align: center;
}

.modal__text {
  font-family: inherit;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
  padding-bottom: 21px;
  font-size: 0.93rem;
  line-height: 1.87rem;
  color: #444444;
  text-align: center;
}

.modal__cart {
  display: flex;
  flex-direction: column;
  min-width: 16.25rem;
}

.modal__cart-list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 3rem;
  padding: 0;
  list-style: none;
}

.modal__control-span {
  padding: 12px 34px;
  background: #ffffff;
  border: 2px solid rgba(68, 68, 68, 0.1);
}

.modal__control-span:hover {
  border: 2px solid rgba(68, 68, 68, 0.3);
}

.modal__control-input:checked + .modal__control-span {
  border: 2px solid #62d1ba;
}

.modal__button {
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
}

.modal__button:hover,
.modal__button:active {
  border: none;
}

@media (min-width: 768px) {
  .modal__container {
    min-width: 566px;
    padding: 2.3rem 2rem 2.8rem;
  }

  .modal__title {
    max-width: 100%;
    margin-bottom: 0.7rem;
    font-size: 30px;
    line-height: 1.9em;
  }

  .modal__text {
    margin: 0;
    margin-bottom: 2.2rem;
    padding: 0;
    font-size: 17px;
    line-height: 30px;
  }

  .modal__cart {
    max-width: 350px;
    margin: auto;
  }

  .modal__control-span {
    padding: 12px 49px;
  }
}
/* index */
.navigation {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  padding: 0;
}

.navigation__logo {
  margin: auto;
  padding: 22px;
  padding-left: 20px;
  padding-bottom: 19px;
  grid-column: 2;
}

.navigation__logo-image {
  min-width: 85px;
}
.navigation__logo-image:hover {
  opacity: 0.6;
}
.navigation__logo-image:active {
  opacity: 0.2;
}

.navigation__button {
  display: flex;
  justify-self: flex-end;
  position: absolute;
  top: 32px;
  right: 21px;
  width: 30px;
  height: 17px;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  border: none;
}

.page-header__closed .navigation__button::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 #000000;
}

.page-header__opened .navigation__button::before, .page-header__opened .navigation__button::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -3px;
  width: 26px;
  height: 2px;
  background-color: #000000;
}
.page-header__opened .navigation__button::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__opened .navigation__button::after {
  transform: rotate(-45deg);
}

.navigation__list {
  margin: 0;
  padding: 0;
  grid-column: 1/-1;
  list-style: none;
}

.navigation__item {
  padding: 26px 0 23px;
  text-align: center;
  box-shadow: inset 0 1px 0 #ececec;
}

.navigation__link {
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  font-size: 17px;
}
.navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}

.navigation__link-icon {
  opacity: 0.2;
}

.navigation__link:hover .navigation__link-icon {
  fill: #63d1bb;
  opacity: 0.6;
}

.navigation__link:active .navigation__link-icon {
  fill: rgba(99, 209, 187, 0.3);
  opacity: 1;
}

.navigation-additional__link {
  font-size: 15px;
}
.navigation-additional__link:hover, .navigation-additional__link:focus {
  color: rgba(0, 0, 0, 0.6);
}
.navigation-additional__link:active {
  color: rgba(0, 0, 0, 0.3);
}

@media (max-width: 767px) {
  .navigation__list {
    position: absolute;
    z-index: 1;
    top: 100%;
    width: 100%;
    background-color: #ffffff;
  }

  .site-navigation .navigation__item {
    padding-left: 4.8rem;
    padding-right: 4.8rem;
    text-align: start;
  }

  .user-navigation {
    top: 300%;
  }

  .user-navigation .navigation__item {
    padding-left: 2rem;
    padding-right: 2rem;
    text-align: start;
  }

  .navigation__search-text,
.navigation__cart--text {
    padding-left: 1.4rem;
  }

  .page-header .container {
    width: 100%;
  }

  .page-header__closed .navigation__list {
    display: none;
  }

  .page-header__nojs .navigation__button {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation {
    grid-template-columns: 0.8fr 1fr;
  }

  .navigation__logo {
    margin: 0;
    padding: 22px 0 17px;
    grid-column: 1;
  }

  .navigation__logo-image {
    width: 113px;
    height: 46px;
  }

  .navigation__button {
    display: none;
  }

  .navigation__list {
    margin: 0;
  }

  .navigation__item {
    padding: 29px 0 26px;
    box-shadow: none;
  }

  .navigation__cart {
    padding-left: 33px;
  }

  .navigation__cart--text {
    margin-left: 25px;
  }

  .site-navigation {
    display: grid;
    position: absolute;
    top: 5rem;
    gap: 1.8rem;
    grid-template-columns: 1fr 1fr;
    letter-spacing: -1px;
    z-index: 1;
  }

  .site-navigation .navigation__item {
    padding: 26px 0;
  }

  .user-navigation {
    display: grid;
    grid-template-columns: 0.65fr 2fr;
    grid-column: 2/4;
    margin-left: auto;
  }

  .user-navigation .navigation__item {
    margin: auto 0;
    border-left: 1px solid #ececec;
  }

  .navigation__search-text {
    display: none;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 0.5fr 1fr;
  }

  .navigation__logo {
    margin: 0 auto;
    padding: 9px 0 4px;
    order: 2;
    grid-column: auto;
  }

  .navigation__logo-image {
    width: 149px;
    height: 72px;
  }

  .site-navigation .navigation__item {
    border-right: 1px solid #ececec;
    text-align: start;
  }

  .navigation__list {
    grid-column: auto;
  }

  .site-navigation {
    display: grid;
    position: relative;
    top: 0;
    grid-template-columns: 0.9fr 1fr;
    gap: 2rem;
  }

  .user-navigation {
    order: 3;
  }

  .navigation__cart {
    padding-left: 29px;
  }

  .navigation__cart--text {
    margin-left: 29px;
  }
}
