.advantages {
  margin-bottom: 30px;
}

.advantages__title {
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  margin: 0;
  margin-bottom: 28px;
  font-size: 25px;
  line-height: 1.2em;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.advantages__item {
  position: relative;
  max-width: 96%;
  padding-top: 54px;
  padding-bottom: 28px;
  line-height: 20px;
}
.advantages__item::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 55px;
  height: 38px;
  content: "";
}

.advantages__item--flover-icon::before {
  left: 8px;
  background-image: url("../img/icon-flover.svg");
  background-repeat: no-repeat;
}

.advantages__item--money-icon {
  padding-bottom: 30px;
}

.advantages__item--money-icon::before {
  top: 1px;
  background-image: url("../img/icon-money.svg");
  background-repeat: no-repeat;
}

.advantages__item--heart-icon::before {
  top: 5px;
  left: 7px;
  background-image: url("../img/icon-heart.svg");
  background-repeat: no-repeat;
}

.advantages__item--yarn-icon::before {
  top: 3px;
  left: 3px;
  background-image: url("../img/icon-yarn.svg");
  background-repeat: no-repeat;
}

.advantages__item--rocket-icon {
  padding-bottom: 31px;
}

.advantages__item--rocket-icon::before {
  top: 5px;
  left: -6px;
  background-image: url("../img/icon-rocket.svg");
  background-repeat: no-repeat;
}

.advantages__item--gift-icon::before {
  top: 1px;
  left: 3px;
  background-image: url("../img/icon-gift.svg");
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
  .advantages {
    margin-bottom: 25px;
  }

  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .advantages__title {
    margin-bottom: 41px;
    font-size: 43px;
  }

  .advantages__item {
    width: 90%;
    padding-top: 56px;
