.radio-label,
.control__checkbox {
  position: relative;
  cursor: pointer;
  line-height: 18px;
}

.radio-label::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -60px;
  width: 42px;
  height: 42px;
  border: 2px solid #444444;
  border-radius: 50%;
}

.radio-input:checked + .radio-label::before {
  background-image: url("../img/icon-circle.svg");
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: center;
}

.radio-label:hover::before {
  border-color: #62d1ba;
}

.radio-label:active::before {
  border-color: rgba(0, 0, 0, 0.3);
}

.control__checkbox::before {
  content: "";
  position: absolute;
  top: -8px;
  left: -58px;
  width: 38px;
  height: 38px;
  border: 2px solid #444444;
}

.control__input:checked ~ .control__checkbox::before {
  background-image: url("../img/icon-chek.svg");
  background-repeat: no-repeat;
  background-size: 22px 22px;
  background-position: center;
}

.control__checkbox:hover::before {
  border-color: #62d1ba;
}

.control__checkbox:active::before {
  border-color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) {
  .radio-label::before {
    top: -13px;
    left: -76px;
    width: 50px;
    height: 50px;
  }

  .radio-input:checked + .radio-label::before {
    background-size: 21px 21px;
  }

  .control__checkbox::before {
    top: -11px;
    left: -79px;
    width: 51px;
    height: 51px;
  }

  .radio-label,
.control__checkbox {
    font-size: 19px;
    line-height: 18px;
  }
}
